import React, { Component } from 'react';
import './Portfolio.css'

class Porfilio extends Component {
    render() { 
        if(this.props.data){
            var projects = this.props.data.projects.map((projects) => {
                var projectImage = 'image/portfolio/' + projects.image;
                return(
                    <div key={projects.title} className="columns portfolio-item">
                        <div className="item-wrap">
                            <a href={projects.url} title={projects.name}>
                                <img alt={projects.name} src={projectImage} />
                                <div className="overlay">
                                    <div className="portfolio-item-meta">
                                        <h5>{projects.title}</h5>
                                        <p>{projects.category}</p>
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                )
            })
        }
        return ( 
            <section id="portfolio">
                <div className="row">
                    <div className="twelve columns collapsed">
                        <h1>Check Out Some of My Work!</h1>
                        <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                            {projects}
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}
 
export default Porfilio;